
// Make viewport great again (responsive)
// http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/
@at-root {

  @-ms-viewport { // stylelint-disable-line  at-rule-no-vendor-prefix
    width: device-width;
  }
}

.page { // html

  $block-name:                &; // #{$block-name}__element

  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%; // stylelint-disable-line  property-no-vendor-prefix
  min-width: 320px;
  min-height: 100%;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex-grow: 1;
  }

  &__footer-wrapper {
    flex-shrink: 0;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #fff;
  margin: 0;
  min-height: 100%;
}

[tabindex='-1']:focus {
  outline: none !important; // stylelint-disable-line  declaration-no-important
}

// iOS 'clickable elements' fix for role='button'
[role='button'] {
  cursor: pointer;
}

// We love Safari
input[type='search'] {
  -webkit-appearance: textfield; // stylelint-disable-line  property-no-vendor-prefix
}

// Avoid 300ms click delay on touch devices that support the `touch-action` CSS property.
a,
// area,
button,
[role='button'],
input,
label,
select,
// summary,
textarea {
  touch-action: manipulation;
}

// We love EDGE! This is a browser!
svg {

  a &,
  button & {
    pointer-events: none;
  }
}

img {
  max-width: 100%;
  height: auto;
}

/*===================================
=            Main styles            =
===================================*/
main {
  overflow: hidden;
}

h3 {
  display: none;
}

section {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 100vh;

  text-align: center;

  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;

  &:nth-of-type(1){
    background-image: url('https://unsplash.it/1024/683?image=1068');
  }
  &:nth-of-type(2){
    background-image: url('https://unsplash.it/1024/683?image=1069');
  }
  &:nth-of-type(3){
    background-image: url('https://unsplash.it/1024/683?image=1070');
  }
  &:nth-of-type(4){
    background-image: url('https://unsplash.it/1024/683?image=1071');
  }
}

p {
  margin: 0;
  font-size: 2rem;
  padding: 0 5rem;
  text-shadow: 1px 1px #333;
  color: #fff;
}
/*=====  End of Main styles  ======*/